import { AddIcon } from "@chakra-ui/icons"
import { HStack, IconButton, Input } from "@chakra-ui/react"
import { useRef } from "react"

type TodoInputProps = {
  isLoading?: boolean,
  onButtonClick?: (inputStr: string) => void 
}

const TodoInput : React.FC<TodoInputProps> = ({
  isLoading = false,
  onButtonClick = str => {}
}) => {
  const inputRef = useRef<HTMLInputElement>(null)

  function onButtonClickWrapper() {
    if (!inputRef.current || inputRef.current.value === '')
      return
    onButtonClick(inputRef.current.value)
  }

  return (
    <HStack width={"90%"} justifyContent="center" mt={4}>
      <Input flexGrow={1} placeholder="add new task..." minWidth="300px" ref={inputRef} />
      <IconButton isLoading={isLoading} aria-label={""} colorScheme="blue" icon={<AddIcon />} onClick={() => onButtonClick(inputRef.current?.value || '')}/>
    </HStack>
  )
}

export default TodoInput