import { Flex, useBoolean } from "@chakra-ui/react";
import { useEffect, useState } from "react";
import { Task } from "../model/Task";
import TodoList from "../component/TodoList";
import  {useRequest } from "../hook/useRequest";
import TodoInput from "../component/TodoInput";
import { Set } from "immutable";
import TextLoading from "../component/TextLoading";

function sleep(n: number) {
  return new Promise(resolve => {
    setTimeout(() => resolve(null), n)
  })
}

let Main = () => {
  const [tasks, setTasks] = useState<Task[]>([])
  const [taskLoadings, setTaskLoadings] = useState<Set<number>>(Set())

  const [addBtnLoading, adBtnLoadingOpt] = useBoolean(false)

  const taskAddRequester = useRequest('TASK_ADD')
  const taskGetRequester = useRequest('TASK_GET_ALL')
  const taskToggleRequester = useRequest('TASK_TOGGLE')

  const [todoListLoading, todoListLoadingOpt] = useBoolean(true)
  taskGetRequester.useInitEffect({})
  taskGetRequester.useSuccessEffect(tasks => {
    setTasks(tasks.data)
    adBtnLoadingOpt.off()
    todoListLoadingOpt.off()
    setTaskLoadings(Set())
  })

  taskGetRequester.useNotSuccessEffect(fail => {
    alert('request failed')
    console.error(fail)
    todoListLoadingOpt.off()
  })

  taskAddRequester.useSuccessEffect(() => {
    taskGetRequester.doFetch({})
  })
  
  function addBtnClick(taskStr: string) {
    taskAddRequester.doFetch({taskStr})
    adBtnLoadingOpt.on()
    todoListLoadingOpt.on()
  }

  function onTaskToggleClick(taskId: number) {
    // todoListLoadingOpt.on()
    taskToggleRequester.doFetch({id: taskId})
    setTaskLoadings(taskLoadings.add(taskId))
  }

  taskToggleRequester.useSuccessEffect(() => {
    taskGetRequester.doFetch({})
  })

  taskToggleRequester.useNotSuccessEffect(fail => {
    alert('request failed')
    console.error(fail)
  })

  return (
    <Flex mt={2} flexDirection="column" wrap="wrap" gap={2} alignItems='center'>
      <TodoInput isLoading={addBtnLoading} onButtonClick={addBtnClick} />

      <TextLoading isLoading={todoListLoading}>
        <TodoList taskLoadings={taskLoadings} tasks={tasks} onTaskToggleClick={onTaskToggleClick}/> 
      </TextLoading>
    </Flex>
  )
}


export default Main
