
export type Success<T> = {
    _tag: 'SUCCESS',
    data: T
}
export type Fail<T> = {
    _tag: 'FAIL',
    data: T,
}
export type Fatal = {
    _tag: 'FATAL',
    error: Error
}
export type Pending = {
    _tag: 'PENDING'
}

export type Ready = {
    _tag: 'READY'
}

export type Result<U, V = unknown> = Success<U> | Fail<V> | Fatal | Ready | Pending

export type ResultTypes = 'SUCCESS' | 'FAIL' | 'FATAL' | 'PENDING' | 'READY'

export type ResultSuccessType<A> = A extends Success<infer A> ? A : never
export type ResultFailType<A> = A extends Fail<infer A> ? A : never

export function mkSuccess<A, B>(data: A) : Result<A, B> {
    return {
        _tag: 'SUCCESS',
        data
    }
}

export function mkFail<A, B>(data: B) : Result<A, B> {
    return {
        _tag: 'FAIL',
        data
    }
}

export function mkError<A, B>(error: Error | string): Result<A, B> {
    return {
        _tag: 'FATAL',
        error: (error instanceof Error) ? error : new Error(error)
    }
}

export function mkPending<A, B>() : Result<A, B> {
    return {
        _tag: 'PENDING'
    }
}

export function mkReady<A, B>() : Result<A, B> {
    return {
        _tag: 'READY'
    }
}

export type RequestInfo<P, S = unknown, F = unknown> = {
    param: P,
    result: Result<S, F>
}

export function isSuccess<A, B>(result: Result<A, B>) : result is Success<A> {
    return result._tag === 'SUCCESS'
}

export function isFail<A, B>(result: Result<A, B>) : result is Fail<B> {
    return result._tag === 'FAIL'
}

export function isFatal<A, B>(result: Result<A, B>) : result is Fatal {
    return result._tag === 'FATAL'
}

export function isPending<A, B>(result: Result<A, B>) : result is Pending {
    return result._tag === 'PENDING'
}

export function isReady<A, B>(result: Result<A, B>) : result is Ready {
    return result._tag === 'READY'
}

export function mapSuccess<A, B, C>(result: Result<A, B>, fn: (a: A) => C): Result<C, B> {
    if (isSuccess(result))
        return {...result, data: fn(result.data)}
    return {...result}
}

export function mapFail<A, B, C>(result: Result<A, B>, fn: (a: B) => C): Result<A, C> {
    if (isFail(result))
        return {...result, data: fn(result.data)}
    return {...result}
}