import { Alert, AlertIcon, Collapse, Fade, useBoolean, useToast } from "@chakra-ui/react"
import { Fragment, useEffect, useRef, useState } from "react"
import { useNavigate } from "react-router-dom"
import SignInForm, { SignInFormOpt } from "../component/SignInForm"
import { useRequest } from "../hook/useRequest"

const SigninLayout : React.FC = () => {
  const loginRequest = useRequest('USER_LOGIN')
  const navi = useNavigate()
  const formRef = useRef<SignInFormOpt>(null)
  const [alert, alertOpt] = useBoolean(false)
  const [alertStr, setAlertStr] = useState('')
  
  async function onLogin() {
    if (!formRef.current) return
    if (!formRef.current.inputValid()) {
      formRef.current.displayErrorMsg(true)
      return
    }
    formRef.current.displayErrorMsg(false)
    formRef.current.loading(true)
    alertOpt.off()
    loginRequest.doFetch(formRef.current.getState())
  }
  
  loginRequest.useSuccessEffect(result => {
    localStorage.setItem('JWT', result.data)
    navi('/')
  })
  loginRequest.useFailEffect(result => {
    if (!formRef.current) return
    formRef.current.loading(false)
    setAlertStr(result.message)
    alertOpt.on()
  })
  return (
    <Fragment>
      
      <Collapse in={alert}>
      <Alert status='error'>
        <AlertIcon />
          {alertStr}
        </Alert>
      </Collapse>
      <SignInForm 
        ref={formRef}
        loginButtonClick={onLogin}
        createButtonClick={() => navi('/signup')}
      />
    </Fragment>
  )
}
export default SigninLayout