import axios, { AxiosInstance } from "axios"
import { useEffect, useReducer, useState } from "react"
import { Endpoints } from "../../config/config"
import { Task } from "../../model/Task"
import {mkError, mkFail, mkPending, mkSuccess, Pending, RequestInfo, Result, ResultFailType, ResultSuccessType, Success} from './RequestInfo'

type ResponseDto<T> = {
  code: 'OK' | 'BAD_REQUEST' | 'INTERNAL_SERVER_ERROR' | 'NOT_LOGIN'
  message: string,
  data: T 
}

function wrapDto<T>(t: ResponseDto<T>): Result<ResponseDto<T>> {
  if (t.code === 'OK')
    return mkSuccess(t)
  return mkFail(t)
}

// typetype

type RequestSigns = {
  SERVER_ALIVE: RequestInfo<{}, ResponseDto<number>>,
  USER_ISLOGIN: RequestInfo<{},boolean >,
  USER_LOGIN: RequestInfo<{ email: string, passwd: string }, ResponseDto<string>, ResponseDto<void>>,
  USER_SIGNUP: RequestInfo<{ email: string, verCode: string }, ResponseDto<void>>,
  USER_STATUS: RequestInfo<{}, ResponseDto<Record<string, any>>>,
  USER_LOGOUT: RequestInfo<{}, ResponseDto<void>>,
  TASK_GET: RequestInfo<{ id: number }, ResponseDto<Task>>,
  TASK_GET_ALL: RequestInfo<{}, ResponseDto<Task[]>>,
  TASK_GET_VALID: RequestInfo<{},ResponseDto<Task[]>>,
  TASK_GET_UNFINISHED: RequestInfo<{},ResponseDto<Task[]>>,
  TASK_GET_OUTDATED: RequestInfo<{},ResponseDto<Task[]>>,
  TASK_ADD: RequestInfo<{ taskStr: string }, ResponseDto<number>>,
  TASK_TOGGLE: RequestInfo<{ id: number }, ResponseDto<number>>,
  TASK_DEL: RequestInfo<{ id: number }, ResponseDto<void>>
}

type __TypeCheck = RequestSigns[keyof typeof Endpoints] & (typeof Endpoints)[keyof RequestSigns]

const axiosInstance = axios.create({
  withCredentials: true,
})

axiosInstance.interceptors.request.use(function(config) {
  if (config.headers)
    config.headers['Authorization'] = localStorage.getItem('JWT') || ''
  return config
}, function(error) {
  return Promise.reject(error)
})

axiosInstance.interceptors.response.use(function (response) {
  // 2xx 
  // 
  return response;
}, function (error) {
  //  2xx 
  // 
  console.log(error)
  return Promise.resolve(error.response);
});

function toQueryParam(data: Array<[string, any]> | Record<string, any>): string {
  if (Array.isArray(data))
      return data.map(([k, v]) => `${encodeURI(k)}=${encodeURI(v as string)}`).join('&')
  return toQueryParam(Object.entries(data))
}

/**
 * then
 */
const requests : {
  [key in keyof RequestSigns]: (params : RequestSigns[key]['param']) => Promise<RequestSigns[key]['result']>
} = {
    SERVER_ALIVE: function (params: {}): Promise<Result<ResponseDto<number>, unknown>> {
      return axiosInstance.get<ResponseDto<number>>(Endpoints.SERVER_ALIVE)
            .then(r => wrapDto(r.data))
    },
    USER_ISLOGIN: function (params: {}): Promise<Result<boolean, unknown>> {
        return axiosInstance.get(Endpoints.USER_ISLOGIN)
         .then(r => r.data)
    },
    USER_LOGIN: function (params: { email: string; passwd: string }): Promise<Result<ResponseDto<string>, ResponseDto<void>>> {
      return axiosInstance.post<ResponseDto<void>>(Endpoints.USER_LOGIN, toQueryParam(params))
        .then(r => wrapDto(r.data) as any)
    },
    USER_SIGNUP: function (params: { email: string; verCode: string }): Promise<Result<ResponseDto<void>, unknown>> {
      return axiosInstance.post<ResponseDto<void>>(Endpoints.USER_SIGNUP, toQueryParam(params))
          .then(r => wrapDto(r.data))
    },
    USER_STATUS: function (params: {}): Promise<Result<ResponseDto<Record<string, any>>, unknown>> {
      return axiosInstance.get(Endpoints.USER_STATUS)
        .then(r => wrapDto(r.data))
    },
    USER_LOGOUT: function (params: {}): Promise<Result<ResponseDto<void>, unknown>> {
      return axiosInstance.get(Endpoints.USER_LOGOUT)
        .then(r => wrapDto(r.data))
    },
    TASK_GET: function (params: { id: number }): Promise<Result<ResponseDto<Task>, unknown>> {
      return axiosInstance.get(`${Endpoints.TASK_GET}?${toQueryParam({taskId: params.id})}`)
        .then(r => wrapDto(r.data))
    },
    TASK_GET_ALL: function (params: {}): Promise<Result<ResponseDto<Task[]>, unknown>> {
      return axiosInstance.get(Endpoints.TASK_GET_ALL)
        .then(r => wrapDto(r.data))
    },
    TASK_GET_VALID: function (params: {}): Promise<Result<ResponseDto<Task[]>, unknown>> {
      return axiosInstance.get(Endpoints.TASK_GET_VALID)
        .then(r => wrapDto(r.data))
    },
    TASK_GET_UNFINISHED: function (params: {}): Promise<Result<ResponseDto<Task[]>, unknown>> {
      return axiosInstance.get(Endpoints.TASK_GET_UNFINISHED)
        .then(r => wrapDto(r.data))
    },
    TASK_GET_OUTDATED: function (params: {}): Promise<Result<ResponseDto<Task[]>, unknown>> {
      return axiosInstance.get(Endpoints.TASK_GET_OUTDATED)
        .then(r => wrapDto(r.data))
    },
    TASK_ADD: function (params: { taskStr: string }): Promise<Result<ResponseDto<number>, unknown>> {
      return axiosInstance.post(Endpoints.TASK_ADD, [params.taskStr])
        .then(r => wrapDto(r.data))
    },
    TASK_TOGGLE: function (params: { id: number }): Promise<Result<ResponseDto<number>, unknown>> {
      return axiosInstance.post(Endpoints.TASK_TOGGLE, toQueryParam({taskId: params.id}))
              .then(r => wrapDto(r.data))
    },
    TASK_DEL: function (params: { id: number }): Promise<Result<ResponseDto<void>, unknown>> {
      return axiosInstance.post(Endpoints.TASK_DEL, toQueryParam({taskId: params.id}))
              .then(r => wrapDto(r.data))
    }
}


/**
 * generic
 * 
 * 
 * 
 * @param biz 
 * @returns 
 */
export function useRequest<K extends keyof typeof Endpoints>(biz: K): 
  Requester<RequestSigns[typeof biz]['param'], 
            ResultSuccessType<RequestSigns[typeof biz]['result']>, 
            ResultFailType<RequestSigns[typeof biz]['result']>> {
  return useCustomRequest((axios, param) => requests[biz](param as any)) as any
}


export function useCustomRequest<P, A, B>(fn: (axios: AxiosInstance, param: P) => Promise<Result<A, B>> | Promise<A>):Requester<P, A, B> {
  const [c, refetch] = useReducer(s => s + 1, 0)
  const [param, setParam] = useState<P>()
  const [result, setResult] = useState<Result<A, B>>(mkPending())

  useEffect(() => {
    if (!param) return
    // do bussiness logic and set result
    setResult({_tag: 'PENDING'} as Pending)

    let updated = false

    const promise = (async () => {
      try {
        const response = await fn(axiosInstance, param)

        // ""
        if(updated) return

        // await new Promise(resolve => setTimeout(() => resolve({}), 1000)).then()
        
        if ((response as any)._tag){
          setResult(response as Result<A, B>)
        } else {
          setResult(mkSuccess<A, B>(response as Awaited<A>))
        }
      } catch (e) {
        if (typeof e === "string") {
          setResult(mkError(new Error(e)))
        } else if (e instanceof Error) {
          setResult(mkError(e))
        } else {
          console.error('', e)
          setResult(mkError((new Error(': ' + new String(e)))))
        }
      }
    })()

    return () => {
      updated = true
    }

  }, [c])

  function doFetch(param: P) {
    setParam(param)
    refetch()
  }

  return {
    useEffect(handler) {
      useEffect(() => {
        if (result._tag === 'PENDING') return
        handler(result)
      }, [result])
    },
    useSuccessEffect(successFn) {
      useEffect(() => {
        if (result._tag === 'SUCCESS') 
          successFn(result.data)
      }, [result])
    },
    useNotSuccessEffect(fn) {
      useEffect(() => {
        if (result._tag  !== 'PENDING' && result._tag !== 'SUCCESS') 
          fn(result)
      }, [result])
    },
    useFailEffect(failFn) {
      useEffect(() => {
        if (result._tag === 'FAIL') 
          failFn(result.data)
      }, [result])
    },
    useFatalEffect(errorFn) {
      useEffect(() => {
        if (result._tag === 'FATAL') 
          errorFn(result.error)
      }, [result])
    },
    result,
    doFetch,
    useInitEffect(param) {
      useEffect(() => {
        doFetch(param)
      }, [])
    },
  }
}

type Requester<P, A, B = unknown> = {
  useInitEffect: (param: P) => void,
  useEffect: (handler: (result: Result<A, B>) => void) => void,
  useSuccessEffect: (successFn: (result: A) => void) => void,
  useNotSuccessEffect: (fn: (result: Exclude<Result<A, B>, Success<A>>) => void) => void,
  useFailEffect: (failFn: (fail: B) => void) => void,
  useFatalEffect: (errorFn: (error: Error) => void) => void,
  doFetch: (param: P) => void,
  result: Result<A, B>
}