import { Fragment, useEffect, useMemo, useState } from "react"

const DOTS = ['', '.', '..', '...','....','.....']

type TextLoadingProps = {
  isLoading?: boolean,
  children?: JSX.Element,
  text?: string,
  interval?: number,
  [key: string]: any
}

const TextLoading : React.FC<TextLoadingProps> = ({
  text = 'LOADING',
  interval = 300,
  isLoading = true,
  children = null,
  ...others
}) => {
  
  const [textIndex, setTextIndex] = useState(0)

  useEffect(() => {
    const schedule = setInterval(() => {
      setTextIndex(index => index + 1)
    }, interval)
    return () => {
      clearInterval(schedule)
    }
  }, [])

  return (
    <Fragment>
      {isLoading ? <p {...others}>{text}<span>{DOTS[textIndex % DOTS.length]}</span></p> : children}
    </Fragment>
  )
}

export default TextLoading