/**
 * The Form which Sign in will use, it will be a uncontrolled component which contains it's own state.
 * 
 * and there will be questions such as... how to make operations like clear all input? 
 */
import { Button, FormControl, FormErrorMessage, FormLabel, Input, StackItem, Switch, useBoolean, VStack } from "@chakra-ui/react"
import { type } from "os"
import { useEffect, useReducer, forwardRef, useImperativeHandle, useTransition, useState } from "react"
import { useValidationState } from "../hook/useValidationState"
import { validEmail, validPassword } from "../util/Validation"

type LoginData = {
  email: string,
  passwd: string
}

/**
 * caller provided
 */
export type SignInFormProps = {
  loginButtonClick?: () => void,
  createButtonClick?: () => void
}

export type SignInFormOpt = {
  clearInput(): void
  getState(): LoginData
  inputValid(): boolean
  loading(p: boolean): void
  displayErrorMsg(p: boolean): void
}

const SignInForm = forwardRef<SignInFormOpt, SignInFormProps>(({
  loginButtonClick = () => { },
  createButtonClick = () => { } }, ref) => {
  const [email, setEmail, ifEmailValid, emailInvalidReason] = useValidationState<string>('', validEmail)
  const [displayEmailErorr, setDisplayEmailErorr] = useState(false)

  const [passwd, setPasswd, ifPasswdValid, passwdInvalidReason] = useValidationState<string>('', validPassword)
  const [displayPasswdErorr, setDisplayPasswdErorr] = useState(false)

  const [signInBtnLoading, setSignInBtnLoading] = useState(false)

  useEffect(() => {
    setDisplayEmailErorr(false)
  }, [email])

  useEffect(() => {
    setDisplayPasswdErorr(false)
  }, [passwd])

  useImperativeHandle(
    ref,
    () => ({
      clearInput() {
        setEmail('')
        setPasswd('')
      },
      getState() {
        return {
          email,
          passwd
        }
      },
      inputValid() {
        return ifEmailValid && ifPasswdValid
      },
      loading(p) {
        setSignInBtnLoading(p)
      },
      displayErrorMsg(p) {
        setDisplayEmailErorr(p)
        setDisplayPasswdErorr(p)
      },
    })
  )
  
  return (
    <VStack>
      <StackItem minWidth={'320px'}>
        <FormControl isInvalid={displayEmailErorr && !ifEmailValid}>
          <FormLabel>Email address</FormLabel>
          <Input type='email' value={email} onChange={e => setEmail(e.target.value)} />
          <FormErrorMessage>{emailInvalidReason}</FormErrorMessage>
        </FormControl>
      </StackItem>
      <StackItem minWidth={'320px'}>
        <FormControl marginTop={1} marginBottom={2} isInvalid={displayPasswdErorr && !ifPasswdValid}>
          <FormLabel>password</FormLabel>
          <Input type='password' value={passwd} onChange={e => setPasswd(e.target.value)} />
          <FormErrorMessage>{passwdInvalidReason}</FormErrorMessage>
        </FormControl>
      </StackItem>
      <StackItem minWidth={'320px'}>
        <Button width={"100%"} colorScheme={'blue'} onClick={loginButtonClick} isLoading={signInBtnLoading}>
          Sign In
        </Button>
      </StackItem>
      <StackItem minWidth={'320px'}>
        <Button width={"100%"} onClick={createButtonClick}>Create new Account</Button>
      </StackItem>
    </VStack>
  )
})

export default SignInForm